@font-face {
  font-family: 'kelvin-pofo-2025';
  src:
    url('../fonts/kelvin-pofo-2025.ttf?4ee9m3') format('truetype'),
    url('../fonts/kelvin-pofo-2025.woff?4ee9m3') format('woff'),
    url('../fonts/kelvin-pofo-2025.svg?4ee9m3#kelvin-pofo-2025') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

.icon {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'kelvin-pofo-2025' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[data-icon="linkedin"]:before {
  content: "\e900";
}
[data-icon="email"]:before {
  content: "\e901";
}
[data-icon="codepen"]:before {
  content: "\e902";
}
[data-icon="history"]:before {
  content: "\e903";
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
p {
  margin: 0;
  padding: 0;
}

body {
  font-family: "futura-pt", Futura, sans-serif;
  line-height: 1.1;
  padding: 0.5em;
  background: #000000;
  color: #fff;
  max-width: 640px;
  font-weight: 500;
  margin: 0 auto;
}

