body,
h1,
h2 {
  font-size: 26px;
}

a {
  color: white;
  font-weight: 700;
  text-decoration: none;
}

a:hover {
}

#intro img {
  width: 140px;
  float: right;
  border-radius: 10px;
  margin: 0 0 10px 10px;
}

h1,
h2 {
  font-weight: 700;
}

h2 {
  margin-top: 1em;
  font-style: italic;
}

h1,
h2,
p,
li {
  padding: 0.1em 0;
}

li {
  list-style: none;
  font-weight: 700;
}

.word,
.char {
  display: inline-block;
}

.char {
  position: relative;
}

.credo {
  font-style: italic;
}

.copy {
  font-size: 20px;
}

#contact p {
  margin-bottom: 0.8em;
}

/* desktop sizing */
@media screen and (min-width: 600px) {
  body {
    padding: 1em;
  }

  body,
  h1,
  h2 {
    font-size: 32px;
  }

  #intro img {
    width: 200px;
  }

  .copy {
    font-size: 28px;
  }
}

#intro {
  font-family: "Playfair Display", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
